.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  padding-bottom: 100%;
  text-decoration: none;
  display: block;
  position: relative;
}

.icon__tooltip {
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
  opacity: 0;
  pointer-events: none;
  width: max-content;
  padding-right: 12px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  transition: opacity .3s;
  display: block;
  position: absolute;
  top: 50%;
  right: 100%;
  transform: translateY(-50%);
}

.icon:hover .icon__tooltip {
  opacity: 1;
  pointer-events: all;
}

